.row {
    margin-left: 20px;
    color:white;
}
.row_posters {
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}
.row_posters::-webkit-scrollbar {
    display: none;
}
.row_poster{
    object-fit: contain;
    width: 100%;
    max-height: 100px;
    transition: transform 450ms;
    margin-right: 10px;
}

.row_poster:hover{
    transform: scale(1.08);

}

.row_posterlarge{
    max-height: 250px;
}

.row_posterlarge:hover{
    transform: scale(1.09);

}